.navbar {
  display: flex;
  align-items: center;
  justify-content: space-around;
  width: 100%;
  height: 55px;
  font-weight: 600;
  background-color: rgb(229, 231, 255);
}
.btn1 {
  background-color: rgb(108, 99, 255);
}

.socialicon {
  position: absolute;
  bottom: 30px;
  left: 25px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.socialicon > button {
  width: 40px;
  height: 40px;
  background-color: white;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
  margin-right: 5px;
  border: 2.5px solid rgb(108, 99, 255);
}
.socialicon > button img:nth-child(1) {
  width: 20px;
  height: 20px;
}
.socialicon > button img:nth-child(2) {
  width: 30px;
  height: 30px;
}
.socialicon > button img:nth-child(0) {
  width: 30px;
  height: 30px;
}
@media all and (min-width: 200px) and (max-width: 480px){
  .btn1 {
    display: block;
    margin-left: 30px;
  }
  .navdata {
    display: none;
  }
  .navbar {
    justify-content: space-between;
  }
  .name{
    display: none;
  }
}

@media all and (min-width: 4810px) and (max-width: 750px) {
  .btn1 {
    display: block;
    margin-left: 30px;
  }
  .navdata {
    display: none;
  }
  .navbar {
    justify-content: space-between;
  }
}
@media all and (min-width: 751px) and (max-width: 3000px) {
  .btn1 {
    display: none;
  }
}
