.skillItem {
    position: relative;
    width: 130px;
    height: auto;
    min-height: 150px;
    border-radius: 7px;
    border: 1px solid #ccc;
    padding: 15px;
    background-color: #fff;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
    margin-right: 30px;
    margin-bottom: 30px;
    /* z-index: 2; */
  }
  
  .skillItemAfter {
    content: "";
    position: absolute;
    top: -3px;
    right: -3px;
    width: 130px;
    min-height: 150px;
    height: auto;
    border-radius: 7px;
    padding: 15px;
    border: 1px solid #ccc;
    background-color: #fff;
    z-index: -1;
  }
  
  .skillItemBefore {
    content: "";
    position: absolute;
    top: -5px;
    right: -5px;
    width: 130px;
    min-height: 150px;
    height: auto;
    border-radius: 7px;
    padding: 15px;
    border: 1px solid #ccc;
    background-color: #fff;
    z-index: -2;
  }
  
  .skillImage {
    width: 60px;
    height: auto;
  }
  
  .skillName {
    font-size: 18px;
    font-family: "Nunito-Bold";
    margin-top: 20px;
  }
  
  .skillStars {
    margin-top: auto;
  }
  
  @media screen and (max-width: 402px) {
    .skillItem {
      margin-right: 0;
    }
  }